import axios from 'axios'
import { throwErr } from './throwErr' // utils http
// import store from '@/store' // vuex
import { Toast } from 'mint-ui' // mint Toast
// import router from '@/router'

// axios.defaults.baseURL = 'http://148.70.56.228/index.php'

// 
axios.interceptors.request.use(config => {
  // config    Token  axios  
  // 
  config.timeout = 10 * 1000 // 
  return config
}, error => {
  return Promise.reject(error)
})
// 
axios.interceptors.response.use(
  response => {
    // console.log(response.data.code)
    if (response.data.code === '200' || response.data.code === 1) { // code0
      return Promise.resolve(response.data) // Promise.resolve 
    } else if (response.data.code === 1401) { // code1401
      // store.dispatch('setUserInfo', {})
      Toast({
        message: '',
        position: 'bottom'
      })
      // router.push('/login')
      return Promise.reject(response.data) // Promise.reject 
    } else {
      return Promise.reject(response.data)
    }
  },
  error => {
    if (error && error.response) {
      let res = {}
      res.code = error.response.status
      res.msg = throwErr(error.response.status, error.response) // throwErr http utils
      return Promise.reject(res)
    }
    return Promise.reject(error)
  }
)
export default function request (method, url, data, dev) { //  request API 
  if (dev === 'prod') {
    axios.defaults.baseURL = 'http://148.70.56.228/index.php'
  } else {
    axios.defaults.baseURL = ''
  }

  method = method.toLocaleLowerCase() // RESTful API  post get delete
  if (method === 'post') {
    return axios.post(url, data) // axiospost json
  } else if (method === 'get') {
    return axios.get(url, {
      params: data
    })
  } else if (method === 'delete') {
    return axios.delete(url, {
      params: data
    })
  }
}



